/* -------------------------------------------------- */
/* TIPS
/* -------------------------------------------------- */

.tip#{$modifier} {

	&#{'top'}::after,
	&#{'right'}::after,
	&#{'bottom'}::after,
	&#{'left'}::after {
		@extend .pseudo;
		color: $white;
		font-weight: 700;
		line-height: 0.75em;
		text-transform: uppercase;
	}

	&#{'top'}::after {
		top: 0;
		left: 50%;
		transform: translateX(-50%) rotate(45deg);
	}

	&#{'right'}::after {
		top: 50%;
		right: 0;
		transform: translateY(-50%) rotate(45deg);
	}

	&#{'bottom'}::after {
		bottom: 0;
		left: 50%;
		transform: translateX(-50%) rotate(45deg);
	}

	&#{'left'}::after {
		top: 50%;
		left: 0;
		transform: translateY(-50%) rotate(45deg);
	}

}
