/* -------------------------------------------------- */
/* SVG
/* -------------------------------------------------- */

.svg#{$modifier} {

	@include color($type: 'hex', $property: 'stroke', $prefix: '', $suffix: ' *', $alpha: 1);
	@include color($type: 'hex', $property: 'fill', $prefix: '', $suffix: ' *', $alpha: 1);
	@include color($type: 'svg-gradient', $property: 'color', $prefix: '', $suffix: '-gradient path', $alpha: 1);

	& {
		@include color($type: 'hex', $property: 'stroke', $prefix: '', $suffix: '-hover:hover *', $alpha: 1);
		@include color($type: 'hex', $property: 'fill', $prefix: '', $suffix: '-hover:hover *', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'stroke', $prefix: '', $suffix: ':active *', $alpha: 1);
		@include color($type: 'hex', $property: 'fill', $prefix: '', $suffix: ':active *', $alpha: 1);
	}

	&#{'global'} {
		z-index: -1;
		position: absolute;
		pointer-events: none;
		opacity: 0;
	}

	&#{'stroke-none'},
	&#{'stroke-none *'},
	&#{'stroke-none g'},
	&#{'stroke-none g *'} {
		stroke: none !important;
	}

	&#{'fill-none'},
	&#{'fill-none *'},
	&#{'fill-none g'},
	&#{'fill-none g *'} {
		fill: none !important;
	}

}


/* -------------------------------------------------- */
/* TEXT
/* -------------------------------------------------- */

.text#{$modifier} {

	@include color($type: 'hex', $property: 'color', $prefix: '', $suffix: '', $alpha: 1);

	& {
		@include color($type: 'hex', $property: 'color', $prefix: '', $suffix: '-hover:hover', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'color', $prefix: '', $suffix: ':active', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'color', $prefix: '', $suffix: '-focus:focus', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'color', $prefix: '', $suffix: ':visited', $alpha: 1);
	}

}


/* -------------------------------------------------- */
/* DIVIDERS (HORIZONTAL RULE)
/* -------------------------------------------------- */

.hr#{$modifier} {

	@include color($type: 'hex', $property: 'background-color', $prefix: '', $suffix: '', $alpha: 1);

	& {
		@include color($type: 'hex', $property: 'background-color', $prefix: '', $suffix: '-hover:hover', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'background-color', $prefix: '', $suffix: ':active', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'background-color', $prefix: '', $suffix: '-focus:focus', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'background-color', $prefix: '', $suffix: ':visited', $alpha: 1);
	}

}


/* -------------------------------------------------- */
/* BORDERS
/* -------------------------------------------------- */

.border#{$modifier} {

	@include color($type: 'hex', $property: 'border-color', $prefix: '', $suffix: '', $alpha: 1);
	@include color($type: 'gradient', $property: 'border-image', $prefix: '', $suffix: '', $alpha: 1);

	& {
		@include color($type: 'hex', $property: 'border-color', $prefix: '', $suffix: '-hover:hover', $alpha: 1);
		@include color($type: 'gradient', $property: 'border-image', $prefix: '', $suffix: '-hover:hover', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'border-color', $prefix: '', $suffix: ':active', $alpha: 1);
		@include color($type: 'gradient', $property: 'border-image', $prefix: '', $suffix: ':active', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'border-color', $prefix: '', $suffix: '-focus:focus', $alpha: 1);
		@include color($type: 'gradient', $property: 'border-image', $prefix: '', $suffix: '-focus:focus', $alpha: 1);
	}

}


/* -------------------------------------------------- */
/* BACKGROUNDS
/* -------------------------------------------------- */

.background#{$modifier} {

	@include color($type: 'hex', $property: 'background-color', $prefix: '', $suffix: '', $alpha: 1);

	& {
		@include color($type: 'hex', $property: 'background-color', $prefix: '', $suffix: '-hover:hover', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'background-color', $prefix: '', $suffix: ':active', $alpha: 1);
	}

	& {
		@include color($type: 'hex', $property: 'background-color', $prefix: '', $suffix: '-focus:focus', $alpha: 1);
	}

	&#{'white-25'} {
		background-color: rgba($white, 0.25);
	}

	&#{'white-50'} {
		background-color: rgba($white, 0.5);
	}

	&#{'white-75'} {
		background-color: rgba($white, 0.75);
	}

	&#{'white-90'} {
		background-color: rgba($white, 0.9);
	}

	&#{'black-25'} {
		background-color: rgba($black, 0.25);
	}

	&#{'black-50'} {
		background-color: rgba($black, 0.5);
	}

	&#{'black-75'} {
		background-color: rgba($black, 0.75);
	}

	&#{'black-90'} {
		background-color: rgba($black, 0.9);
	}

}


/* -------------------------------------------------- */
/* GRADIENTS
/* -------------------------------------------------- */

.background#{$modifier} {

	@include color($type: 'gradient', $property: 'background-image', $prefix: '', $suffix: '', $alpha: 1);

	& {
		@include color($type: 'gradient', $property: 'background-image', $prefix: '', $suffix: '-hover:hover', $alpha: 1);
	}

	& {
		@include color($type: 'gradient', $property: 'background-image', $prefix: '', $suffix: ':active', $alpha: 1);
	}

	& {
		@include color($type: 'gradient', $property: 'background-image', $prefix: '', $suffix: '-focus:focus', $alpha: 1);
	}

