/* -------------------------------------------------- */
html {
  visibility: hidden;
  overflow-x: hidden;
  overflow-y: scroll; }
  html.is-locked {
    overflow-x: hidden;
    overflow-y: hidden; }
  html.no-js {
    visibility: visible;
    overflow-x: hidden;
    overflow-y: hidden;
    background-color: #29292e; }
    html.no-js body main {
      display: none; }

