/* ERROR: JS DISABLED
/* -------------------------------------------------- */
/* PARENT */
noscript {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0; }
  noscript div {
    position: absolute;
    top: 50%;
    left: 50%;
    font: normal normal 1rem/100% -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, Ubuntu, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 1em;
    margin: 0;
    padding: 0;
    width: 100%;
    max-width: 300px;
    color: #a71478;
    text-align: center; }
    noscript div img {
      display: block;
      width: 75%;
      margin: auto;
      padding-bottom: 1rem; }
    noscript div h1 {
      font-size: 1em; }
    noscript div p {
      font-weight: bold; }

/* -------------------------------------------------- */
/* ERROR: WINDOW RESIZE
/* -------------------------------------------------- */
/* PARENT */
#error {
  overflow: hidden;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  min-height: 100%;
  background-color: #29292e;
  /*
		@media screen and ( min-width: $min ) and ( max-width: $max ) {
			@content;
		}
		*/ }
  #error p {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 0 0.75rem 0 0.75rem;
    width: 100%;
    color: white;
    font: normal normal 400 1rem/100% pt-serif, serif;
    font-size: 1.25em;
    text-align: center;
    transform: translateX(-50%) translateY(-50%); }
  @media screen and (min-width: 1px) and (max-width: 319px) {
    #error {
      z-index: 9999;
      display: block; } }

/*
		@media screen and ( min-width: $min ) and ( max-width: $max ) {
			@content;
		}
		*/
@media screen and (min-width: 1px) and (max-width: 319px) {
  html {
    overflow-x: hidden;
    overflow-y: hidden; } }

