/* -------------------------------------------------- */
/* TIPS
/* -------------------------------------------------- */
.tip-top::after, .tip-right::after, .tip-bottom::after, .tip-left::after {
  color: white;
  font-weight: 700;
  line-height: 0.75em;
  text-transform: uppercase; }

.tip-top::after {
  top: 0;
  left: 50%;
  transform: translateX(-50%) rotate(45deg); }

.tip-right::after {
  top: 50%;
  right: 0;
  transform: translateY(-50%) rotate(45deg); }

.tip-bottom::after {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) rotate(45deg); }

.tip-left::after {
  top: 50%;
  left: 0;
  transform: translateY(-50%) rotate(45deg); }

