/* TYPOGRAPHY
/* -------------------------------------------------- */
/* -------------------------------------------------- */
/* BLOCKQUOTES
/* -------------------------------------------------- */
.blockquote {
  position: relative;
  width: 100%;
  border-left: 1px solid #a71478;
  /*
		@media screen and ( min-width: $min ) and ( max-width: $max ) {
			@content;
		}
		*/
  /*
		@media screen and ( min-width: $min ) and ( max-width: $max ) {
			@content;
		}
		*/ }
  .blockquote::before {
    font-family: "Font Awesome 5 Pro";
    font-weight: 900;
    content: '\f10d';
    position: "solid";
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    z-index: -1;
    opacity: 0.5;
    position: absolute;
    top: 0;
    left: -60px;
    color: #39383e;
    font-size: 2em;
    transform: translate(-10%, 0);
    /*
		@media screen and ( min-width: $min ) and ( max-width: $max ) {
			@content;
		}
		*/ }
    @media screen and (min-width: 1px) and (max-width: 1366px) {
      .blockquote::before {
        opacity: 1;
        left: 25px;
        color: #29292e;
        text-shadow: -1px -1px 0 #a71478, 1px -1px 0 #a71478, -1px 1px 0 #a71478, 1px 1px 0 #a71478; } }
  .blockquote p {
    padding-left: 3rem;
    padding-bottom: 1rem;
    color: #c8c8c8;
    font-size: 1.5em;
    line-height: 1.5em;
    /*
		@media screen and ( min-width: $min ) and ( max-width: $max ) {
			@content;
		}
		*/ }
    @media screen and (min-width: 1px) and (max-width: 667px) {
      .blockquote p {
        padding-left: 0;
        font-size: 1em; } }
  .blockquote footer {
    padding-left: 3rem;
    line-height: 0.75em;
    /*
		@media screen and ( min-width: $min ) and ( max-width: $max ) {
			@content;
		}
		*/ }
    .blockquote footer cite {
      color: #39383e;
      font: normal normal 800 1rem/100% montserrat, sans-serif;
      font-size: 0.6em;
      letter-spacing: 0.125em; }
      .blockquote footer cite .blockquote-author {
        display: inline-block;
        position: relative;
        margin-left: 0;
        margin-right: 0.25rem;
        color: #39383e; }
      .blockquote footer cite .blockquote-company {
        display: inline-block;
        position: relative;
        margin-left: 0.25rem;
        margin-right: 0;
        color: #c8c8c8; }
    @media screen and (min-width: 1px) and (max-width: 667px) {
      .blockquote footer {
        padding-left: 0; } }
  @media screen and (min-width: 1px) and (max-width: 667px) {
    .blockquote {
      padding-top: 3rem;
      padding-right: 2rem;
      padding-bottom: 3rem;
      padding-left: 2rem;
      border-left: 0 solid #a71478; } }
  @media screen and (min-width: 1px) and (max-width: 1024px) {
    .blockquote {
      margin-left: auto;
      margin-right: auto;
      width: 100%; } }

